<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" fullscreen @close="resetForm">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基本信息" name="first">
                <el-form ref="dialogForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="工商执照注册号" prop="creditCode">
                                <el-input v-model="dialogForm.creditCode" placeholder="请填写工商执照注册号" maxlength="18"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="企业名称" prop="companyName">
                                <el-input v-model="dialogForm.companyName" placeholder="请填写企业名称" maxlength="100"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="企业类别" prop="companyCategory">
                                <el-select placeholder="请选择企业类别" clearable v-model="dialogForm.companyCategory">
                                    <el-option v-for="(item, index) in dic['企业类别']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item prop="houseId">
                                <el-popover placement="right" width="400" trigger="click" v-model="showPopover">
                                    <techo-house ref="vCheckedHouse" :isShowHouse="true" :butName="'确定'" :disCodeOption="disCodeOption"
                                        :buildOptions="buildOptions" :houseOptions="houseOptions" @onRelaData="handleCheckedHouse"
                                        @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                                    <el-button class="check-house" slot="reference" type="success" size="small">请选择住所户</el-button>
                                </el-popover>
                            </el-form-item>
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="企业地址" prop="companyAddress">
                                <el-input v-model="dialogForm.companyAddress" placeholder="请填写企业地址" maxlength="200"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="企业联系方式" prop="companyContact">
                                <el-input v-model="dialogForm.companyContact" placeholder="请填写企业联系方式" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="企业员工数" prop="companyCount">
                                <el-input v-model="dialogForm.companyCount" placeholder="请输入企业员工数" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="是否危化企业">
                                <el-radio-group v-model="dialogForm.isHarmCompany">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="法定代表人证件类型" prop="legalCardCode">
                                <el-select v-model="dialogForm.legalCardCode" placeholder="请选择法定代表人证件类型">
                                    <el-option v-for="(item, index) in dic['证件代码']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="法定代表人证件号码" prop="legalCardNumber">
                                <el-input v-model="dialogForm.legalCardNumber" placeholder="请填写法定代表人证件号码" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="法定代表人姓名" prop="legalName">
                                <el-input v-model="dialogForm.legalName" placeholder="请填写法定代表人姓名" maxlength="80"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="法定代表人联系方式" prop="legalContact">
                                <el-input v-model="dialogForm.legalContact" placeholder="请填写法定代表人联系方式" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="治保负责人姓名" prop="secuLeaderName">
                                <el-input v-model="dialogForm.secuLeaderName" placeholder="请填写治保负责人姓名" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="治保负责人联系方式" prop="secuLeaderContact">
                                <el-input v-model="dialogForm.secuLeaderContact" placeholder="请填写治保负责人联系方式" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="隐患类型" prop="securityType">
                                <el-select placeholder="请选择隐患类型" v-model="dialogForm.securityType">
                                    <el-option v-for="(item, index) in dic['隐患类型']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="关注程度" prop="concernedDegree">
                                <el-select placeholder="请选择关注程度" v-model="dialogForm.concernedDegree">
                                    <el-option v-for="(item, index) in dic['关注程度']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="second">
                <el-form ref="subForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-form-item label="是否具备建立中共党组织条件" label-width="200px">
                        <el-radio-group v-model="dialogForm.isHaveConditions">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有中共党组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveCommunist">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="中共党员数量" prop="communistCount">
                                <el-input v-model="dialogForm.communistCount" placeholder="请填写中共党员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"></el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有工会" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveUnion">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="工会会员数量" prop="unionCount">
                                <el-input v-model="dialogForm.unionCount" placeholder="请填写工会会员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"></el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有共青团组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveYleague">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="共青团员数量" prop="yleagueCount">
                                <el-input v-model="dialogForm.yleagueCount" placeholder="请填写共青团员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"></el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有妇联组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveWomensorg">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="妇女数量" prop="womenCount">
                                <el-input v-model="dialogForm.womenCount" placeholder="请填写妇女数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"></el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'second'" type="success" size="small" @click="submitForm">保存</el-button>
        </div>
    </el-dialog>
</template>

<script>
import service from "./services/double_origanization_service.js";
export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      title: "",
      dialogVisible: false,
      isAdd: false,
      activeName: "first",
      buildOptions: [],
      houseOptions: [],
      showPopover: false,
      dic: {
        企业类别: [],
        隐患类型: [],
        关注程度: [],
        证件代码: []
      },
      dialogForm: {
        isHarmCompany: 0,
        isHaveConditions: 0,
        isHaveCommunist: 0,
        isHaveUnion: 0,
        isHaveYleague: 0,
        isHaveWomensorg: 0,
        houseId: "",
        districtCode: "",
        companyAddress: "",
        legalCardCode: "111"
      },
      rules: {
        creditCode: [
          {
            required: true,
            message: "请填写工商执照注册号",
            trigger: "blur"
          },
          {
            min: 18,
            max: 18,
            message: "长度为18位",
            trigger: "blur"
          }
        ],
        companyName: [
          {
            required: true,
            message: "请填写企业名称",
            trigger: "blur"
          }
        ],
        companyCategory: [
          {
            required: true,
            message: "请选择企业类别",
            trigger: "change"
          }
        ],
        companyAddress: [
          {
            required: true,
            message: "请填写企业地址",
            trigger: "blur"
          }
        ],
        companyContact: [
          {
            required: true,
            message: "请填写企业联系方式",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        // companyCount: [
        //   {
        //     required: true,
        //     message: "请填写企业员工数",
        //     trigger: "blur"
        //   }
        // ],
        legalCardCode: [
          {
            required: true,
            message: "请选择法定代表人证件类型",
            trigger: "change"
          }
        ],
        legalCardNumber: [
          {
            required: true,
            message: "请填写法定代表人证件号码",
            trigger: "blur"
          }
        ],
        legalName: [
          {
            required: true,
            message: "请填写法定代表人姓名",
            trigger: "blur"
          }
        ],
        legalContact: [
          {
            required: true,
            message: "请填写法定代表人联系方式",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        // secuLeaderName: [
        //   {
        //     required: true,
        //     message: "请填写治保负责人姓名",
        //     trigger: "blur"
        //   }
        // ],
        secuLeaderContact: [
          // {
          //   required: true,
          //   message: "请填写治保负责人联系方式",
          //   trigger: "blur"
          // },
          util.validate.tellAndPhone()
        ],
        // securityType: [
        //   {
        //     required: true,
        //     message: "请选择隐患类型",
        //     trigger: "change"
        //   }
        // ],
        // concernedDegree: [
        //   {
        //     required: true,
        //     message: "请选择关注程度",
        //     trigger: "change"
        //   }
        // ],
        communistCount: [util.validate.valiInteger()],
        unionCount: [util.validate.valiInteger()],
        yleagueCount: [util.validate.valiInteger()],
        womenCount: [util.validate.valiInteger()],
        houseId: [
          { required: true, message: "请选择现住地户", trigger: "change" }
        ]
      }
    };
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["dialogForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      }
    }
  },
  methods: {
    /** 选择现住地户 */
    handleCheckedHouse(value, buildInfo) {
      this.dialogForm.districtCode = buildInfo.districtCode;
      let houseInfo = _.find(this.houseOptions, item => {
        return item.id == value.houseId;
      });
      service.requestCodeByAddress(buildInfo.districtCode, data => {
        this.dialogForm.companyAddress =
          "陕西省" + data.belongname + data.name + houseInfo.name;
        this.showPopover = false;
      });
      this.dialogForm.houseId = houseInfo.id;
      this.$refs["dialogForm"].clearValidate("houseId");
    },
    /** 查询楼栋列表 */
    handleQueryBuildList(code) {
      this.buildOptions = [];
      this.houseOptions = [];
      code != undefined &&
        service.requestCodeByBuild(code, data => {
          this.buildOptions = data.list;
        });
    },
    /** 查询户列表 */
    handleQueryHouseList(param) {
      this.houseOptions = [];
      if (param.buildingId != "") {
        service.requestBuildByHouse(param, data => {
          this.houseOptions = data.list;
        });
      }
    },
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveUnpublicEco(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "新经济组织添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateUnpublicEco(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "新经济组织修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$refs["vCheckedHouse"].resetForm();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.activeName = "first";
      this.dialogForm = {
        isHarmCompany: 0,
        isHaveConditions: 0,
        isHaveCommunist: 0,
        isHaveUnion: 0,
        isHaveYleague: 0,
        isHaveWomensorg: 0,
        houseId: "",
        districtCode: "",
        companyAddress: "",
        legalCardCode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}

.add-dialog .check-house {
    width: 100%;
}
</style>