<template>
  <div class="table-list-page">
    <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
      @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
      @onTreeNode="handleTreeNode">
      <div slot="header">
        <el-row type="flex" justify="space-around" class="techo-list__header-row">
          <el-col :span="10">
            <span class="techo-list__header-col-txt-4">组织名称：</span>
            <div class="techo-list__header-col-left-4">
              <el-input placeholder="请输入组织名称" :maxlength="10" size="small" class="techo-list__header-select-full"
                clearable v-model="queryForm.name"></el-input>
            </div>
          </el-col>
          <el-col :span="9">
          </el-col>
          <el-col :span="5" class="techo-list__header-col-button">
            <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
            <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </techo-list>
    <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    <vAddDialog ref="vAddDialog" :disCodeOption="disCodeOption"></vAddDialog>
  </div>
</template>

<script>
import service from "./services/double_origanization_service.js";
import vAddDialog from "./economics-organization-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "新经济组织",
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        // 工商执照注册号: "creditCode",
        企业名称: "companyName",
        企业类别: "dicMap.companyCategory",
        // 企业地址: "companyAddress",
        企业联系方式: "companyContact",
        // 企业员工数: "companyCount",
        // 法定代表人证件类型: "dicMap.legalCardCode",
        // 法定代表人证件号码: "legalCardNumber",
        法定代表人姓名: "legalName",
        法定代表人联系方式: "legalContact"
        // 治保负责人姓名: "secuLeaderName",
        // 治保负责人联系方式: "secuLeaderContact",
        // 是否危化企业: "dicMap.isHarmCompany",
        // 隐患类型: "dicMap.securityType",
        // 关注程度: "dicMap.concernedDegree",
        // 是否具备建立中共党组织条件: "dicMap.isHaveConditions",
        // 是否有中共党组织: "dicMap.isHaveCommunist",
        // 中共党员数量: "communistCount",
        // 是否有工会: "dicMap.isHaveUnion",
        // 工会会员数量: "unionCount",
        // 是否有共青团组织: "dicMap.isHaveYleague",
        // 共青团员数量: "yleagueCount",
        // 是否有妇联组织: "dicMap.isHaveWomensorg",
        // 妇女数量: "womenCount",
        // 经度: "x",
        // 纬度: "y",
        // 区域编码: "districtCode"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "unPublicEco"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加新经济组织 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
    },
    /** 删除新经济组织 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        _.map(tableSelection, (item, index) => {
          service.requestDeleteUnpublicEco(item.id, data => {
            index == tableSelection.length - 1 && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "新经济组织删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "新经济组织删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo.queryForm = _.cloneDeep(this.queryForm);
      service.requestPageUnpublicEco(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageUnpublicEco(pageInfo, data => {
            this.recordCount = data.totalCount;
            let arr = data.list;
            arr.map(item => {
              if (item.dicMap) {
                for (let key in item) {
                  if (key.match(/^is/)) {
                    item.dicMap[key] = item[key] === 0 ? "否" : "是";
                  }
                }
              }
            });
            this.data = arr;
          });
        } else {
          this.recordCount = data.totalCount;
          let arr = data.list;
          arr.map(item => {
            if (item.dicMap) {
              for (let key in item) {
                if (key.match(/^is/)) {
                  item.dicMap[key] = item[key] === 0 ? "否" : "是";
                }
              }
            }
          });
          this.data = arr;
        }
      });
    }
  }
};
</script>